import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

import { API_CONFIG } from '../../../../config/api.config';
import { CreateEnderecoDto } from '../../interfaces/enderecos/endereco.dto';
import { CreateCondominioDto, CondominioDto, SearchCondominioDto, UpdateCondominioDto } from '../../interfaces/condominios/condominio.dto';
import { PageableDto, PageOptionsDto } from '../../interfaces/others/pageable.dto';
import { PageUtils } from '../../utils/PageUtils';

@Injectable({
  providedIn: 'root'
})
export class CondominioService {

  constructor(
    private http: HttpClient,
  ) { }

  list(filterDto?: SearchCondominioDto, pageOptions = new PageOptionsDto()): Observable<PageableDto<CondominioDto>> {
    let params = PageUtils.getPageOptionsParams(pageOptions);

    if (filterDto && filterDto.searchedCondominio) {
      params = params.append('searchedCondominio', filterDto.searchedCondominio);
    }
    if (filterDto && filterDto.codigo) {
      params = params.append('codigo', filterDto.codigo);
    }
    if (filterDto && filterDto.descricao) {
      params = params.append('descricao', filterDto.descricao);
    }
    if (filterDto && filterDto.nome) {
      params = params.append('nome', filterDto.nome);
    }

    return this.http.get<PageableDto<CondominioDto>>(`${API_CONFIG.baseURL}/condominios`, {
      responseType: 'json',
      params,
    });
  }

  create(newCondominio: CreateCondominioDto): Observable<CondominioDto> {
    return this.http.post<CondominioDto>(
      `${API_CONFIG.baseURL}/condominios`,
      newCondominio,
      {
        responseType: 'json',
      });
  }

  update(updatedCondominio: UpdateCondominioDto, Condominio_id: string): Observable<CondominioDto> {
    return this.http.put<CondominioDto>(
      `${API_CONFIG.baseURL}/condominios/${Condominio_id}`,
      updatedCondominio,
      {
        responseType: 'json',
      });
  }

  createOrUpdateEndereco(Condominio_id: string, newEndereco: CreateEnderecoDto): Observable<any> {
    return this.http.post<any>(
      `${API_CONFIG.baseURL}/condominios/${Condominio_id}/endereco`,
      newEndereco,
      {
        responseType: 'json',
      });
  }
}
